import { useEffect, useState } from "react";
import {
  Getprofile,
  cloudImage,
  countryGet,
  languageGet,
  updateProfilee,
} from "../../../api/login/Login";
import { toast } from "react-toastify";
import { FaCamera } from "react-icons/fa";
import { BiCamera } from "react-icons/bi";
import { baseUrlImage } from "../../../baseUrl";
import {
  Avatar,
  Box,
  Card,
  CardContent,
  Grid,
  Typography,
  Tabs,
  Tab,
  Stack,
} from "@mui/material";
import { Email, Phone } from "@mui/icons-material";
import {
  Person,
  Business,
  VideoCall,
  Gavel,
  CheckCircle,
} from "@mui/icons-material";

const DetailsSection = ({ title, children }) => {
  return (
    <Card
      elevation={0}
      sx={{
        mt: 3,
        borderRadius: 3,
        border: "1px solid rgba(255,255,255,0.08)",
        overflow: "hidden",
        bgcolor: "linear-gradient(180deg, #0f2f44, #123d56)",
        transition: "0.3s",
        "&:hover": {
          boxShadow: "0 12px 30px rgba(0,0,0,0.35)",
        },
      }}
    >
      <Box
        sx={{
          px: 3,
          py: 2,
          // bgcolor: "linear-gradient(135deg,#667eea,#764ba2)",
          background: "linear-gradient(135deg, var(--bs-primary1), var(--bs-secondary1))",
        }}
      >
        <Typography variant="h6" fontWeight={700} color="var(--bs-primary2)">
          {title}
        </Typography>
      </Box>
      <Box>{children}</Box>
    </Card>
  );
};

const DetailRow = ({ label, value }) => {
  return (
    <Stack
      direction="row"
      spacing={2}
      sx={{
        px: 3,
        py: 1.6,
        borderBottom: "1px solid rgba(255,255,255,0.06)",
        "&:last-child": { borderBottom: "none" },
      }}
    >
      <Typography
        variant="h6"
        sx={{
          flex: 1,
          fontSize: 13,
          fontWeight: 600,
          color: "#6b7280",
        }}
      >
        {label} :
      </Typography>
      <Typography
        variant="h6"
        sx={{
          flex: 1,
          fontSize: 14,
          fontWeight: 700,
          color: "#111827",
        }}
      >
        {value || "-"}
      </Typography>
    </Stack>
  );
};

function Profile() {
  const [loader1, setloader1] = useState(false);
  const [country, setCountry] = useState();
  const [language, setLanguage] = useState();
  const [profileImage, setProfileImage] = useState();
  // console.log(profileImage);
  const [data, setData] = useState();
  // console.log(data?.refer_id);

  const [isModalOpen, setModalOpen] = useState(false);

  const toggleModal = () => {
    console.log("Click");
    setModalOpen(!isModalOpen);
  };

  const [initialValues, setInitialValues] = useState({
    name: "",
    email: "",
    mobile: "",
    language_id: "",
    profile: "",
    country: "",
    user_id: "",
    alternate_mobileNo: "",
    state: "",
    country: "",
    presentAddr: "",
    educationQualification: "",
    pinCode: "",
    locking_amt: "",
  });

  const handleChange = (e) => {
    const clone = { ...initialValues };
    const value = e.target.value;
    const name = e.target.name;
    clone[name] = value;
    setInitialValues(clone);
  };

  const getCountry = async () => {
    const res = await countryGet();
    setCountry(res?.data?.data);
  };
  const getlaguage = async () => {
    const res = await languageGet();
    setLanguage(res?.data?.data);
  };

  const imgs = new FormData();
  const [errorMessage, setErrorMessage] = useState("");
  console.log(errorMessage);
  const [imgss, setImgss] = useState(data?.profile);
  // console.log(imgss);
  const colodinaryImage = async (e) => {
  const file = e.target.files[0];
  if (!file) return;

  const imgs = new FormData();
  imgs.append("image", file);

  try {
    const res = await cloudImage(imgs);

    const imageUrl = res?.data?.url; // âœ… correct
    if (!imageUrl) return;

    setImgss(imageUrl);
    updateProfile(imageUrl); // âœ… now works

  } catch (error) {
    console.error(error);
  }
};

  console.log(imgss);

  const toastSuccessMessage = (message) => {
    toast.success(`${message}`, {
      position: "top-center",
      autoClose: 2000,
    });
  };
  const toastSuccessMessage2 = (message) => {
    toast.error(`${message}`, {
      position: "top-center",
      autoClose: 2000,
    });
  };

  const updateProfile = async (url) => {
  setloader1(true);

  const payload = {
    ...initialValues,
    profile: url,
  };

  try {
    const res = await updateProfilee(payload);

    if (res?.status === 200) {
      toast.success(res.data.message);
      getDataProfile(); // ðŸ”¥ refresh UI
    }

  } catch (error) {
    toast.error("Profile update failed");
  } finally {
    setloader1(false);
  }
};


  const getDataProfile = async () => {
    try {
      const res = await Getprofile();
      setData(res?.data?.data);
    } catch (error) {}
  };

  useEffect(() => {
    getCountry();
    getlaguage();
    getDataProfile();
  }, []);

  useEffect(() => {
    const ob = {
      name: data?.name,
      email: data?.email,
      mobile: data?.mobile,
      language_id: data?.language_id,
      country: data?.country,
      profile: data?.profile,
      state: data?.state,
      presentAddr: data?.presentAddr,
      educationQualification: data?.educationQualification,
      pinCode: data?.pinCode,
      locking_amt: data?.locking_amt,
    };
    setInitialValues(ob);
    setImgss(data?.profile);
    // console.log(data?.profile);
  }, [data]);
const storedUserType = localStorage.getItem("userType");
  const [tabValue, setTabValue] = useState(0);
  return (
    <>
      <Card
        sx={{
          borderRadius: 4,
          mb: 3,
          overflow: "hidden",
          border: "1px solid #e5e7eb",
        }}
      >
        <Box
          sx={{
            height: 150,
            // background: "linear-gradient(135deg,#667eea,#764ba2)",
            background: "linear-gradient(200deg, var(--bs-primary1), var(--bs-secondary1))"
          }}
        />
        <Grid container spacing={3} sx={{ p: 3, mt: -9 }}>
          <Grid item xs={12} md={6}>
            <Stack
              direction="row"
              spacing={3}
              marginTop={2}
              alignItems="center"
            >
              {/* <Avatar
              src={`${baseUrlImage}${imgss}`} // Replace with actual image path
              sx={{ width: 80, height: 80 }}
              onClick={toggleModal}
            /> */}
              <Box position="relative">
                <Avatar
                  src={data?.profile||`${baseUrlImage}${imgss}`}
                  sx={{ width: 80, height: 80, cursor: "pointer" }}
                  onClick={toggleModal}
                />
                <label
                  htmlFor="profile-image-upload"
                >
                  <Box
                    sx={{
                      position: "absolute",
                      bottom: 8,
                      right: 8,
                      bgcolor: "white",
                      p: 1,
                      borderRadius: "50%",
                      boxShadow: 2,
                      cursor: "pointer",
                    }}
                  >
                    <FaCamera size={16} />
                  </Box>
                </label>
                <input
                  type="file"
                  id="profile-image-upload"
                  accept="image/*"
                  hidden
                  onChange={colodinaryImage}
                />
              </Box>
              <Box>
                <Typography
                  variant="h6"
                  fontSize={20} fontWeight={800}
                >
                  {initialValues.name}
                </Typography>
                <Typography
                  variant="h6"
                  fontSize={12}
                >
                  Code - {data?.refer_id}
                </Typography>
                <Typography
                  variant="body2"
                  sx={{
                    mt: 1,
                    px: 2,
                    py: 0.5,
                    bgcolor: "#4f46e5",
                    color: "white",
                    borderRadius: 10,
                    display: "inline-block",
                    fontSize: 13,
                    fontWeight: 600,
                  }}
                >
                  {storedUserType}
                </Typography>
              </Box>
            </Stack>
          </Grid>
          <Grid item xs={12} md={6}>
            <Card elevation={0}
              sx={{
                p: 2,
                borderRadius: 2,
                border: "1px solid #e5e7eb",
              }}>
              <Typography
                variant="h6"
                fontWeight={700}
                fontSize={13}
               color="text.secondary">
                CONTACT DETAILS
              </Typography>
              <Stack spacing={1.5} mt={2}>
                <Stack direction="row" spacing={1} alignItems="center">
                  <Email fontSize="small" />
                  <Typography fontSize={14}>{initialValues.email}</Typography>
                </Stack>
                <Stack direction="row" spacing={1} alignItems="center">
                  <Phone fontSize="small" />
                  <Typography fontSize={14}>{initialValues.mobile}</Typography>
                </Stack>
              </Stack>
            </Card>
          </Grid>
        </Grid>
        <Tabs
          value={tabValue}
          onChange={(e, v) => setTabValue(v)}
          sx={{
            px: 3,
            borderBottom: "1px solid rgba(255,255,255,0.1)",
            "& .MuiTab-root": {
              fontWeight: 700,
              textTransform: "none",
            },
          }}
        >
         <Tab icon={<Person />} label="Personal" />
          <Tab icon={<Business />} label="Business" />
          {/* <Tab icon={<VideoCall sx={{ mr: 1 }} />} label="Video/KYC" iconPosition="start" /> */}
          {/* <Tab icon={<Gavel sx={{ mr: 1 }} />} label="Agreements" iconPosition="start" /> */}
          {/* <Tab icon={<CheckCircle sx={{ mr: 1 }} />} label="Whitelisted Accounts" iconPosition="start" /> */}
        </Tabs>
      </Card>
      <Grid container spacing={2}>
        {/* User Details */}
        {tabValue === 0 && (
          <>
            <Grid item xs={12} sm={6}>
              <DetailsSection title="User Details">
                <DetailRow
                  label="Locked Amount"
                  value={initialValues.locking_amt}
                />
                <DetailRow
                  label="Education Qualification"
                  value={initialValues.educationQualification}
                />
                <DetailRow
                  label="Present Address"
                  value={initialValues.presentAddr}
                />
                <DetailRow label="State" value={initialValues.state} />
                <DetailRow label="Pin Code" value={initialValues.pinCode} />
              </DetailsSection>
            </Grid>

            {/* Aadhar Details */}
            <Grid item xs={12} sm={6}>
              <DetailsSection title="Aadhaar Details">
                <DetailRow label="Aadhaar Number" value={data?.adhaar_number} />
                <DetailRow label="Name" value={data?.adhaar_name} />
                <DetailRow label="Date of Birth" value={initialValues.dob} />
                <DetailRow label="District" value={initialValues.district} />
                <DetailRow label="State" value={initialValues.state} />
                <DetailRow label="Country" value={initialValues.country} />
              </DetailsSection>
            </Grid>

            <Grid item xs={12} sm={6}>
              <DetailsSection title="Bank Details">
                <DetailRow
                  label="Account Number"
                  value={data?.bankDetails?.bank_account_number}
                />
                <DetailRow
                  label="IFSC code"
                  value={data?.bankDetails?.bank_ifsc}
                />
                <DetailRow
                  label="Bank Name"
                  value={data?.bankDetails?.bank_name}
                />
                <DetailRow
                  label="Verified Name"
                  value={data?.bankDetails?.bank_account_name}
                />
              </DetailsSection>
            </Grid>
            <Grid item xs={12} sm={6}>
              <DetailsSection title="Pancard Details">
                <DetailRow label="Pancard Name" value={data?.pan_name} />
                <DetailRow label="Pancard Number" value={data?.pan_number} />
              </DetailsSection>
            </Grid>
          </>
        )}
        {tabValue === 1 && (
          <>
            <Grid item xs={12} sm={6}>
              {data?.isGstAvailable ? (
                <DetailsSection title="GST Details">
                  <DetailRow
                    label="GST Number"
                    value={data?.businessInfo?.gst_NUmber}
                  />
                  <DetailRow
                    label="Legal Name of Business"
                    value={data?.businessInfo?.legal_name_of_business}
                  />
                  <DetailRow
                    label="Constitution of Business"
                    value={data?.businessInfo?.constitution_of_business}
                  />
                  <DetailRow
                    label="Taxpayer Type"
                    value={data?.businessInfo?.taxpayer_type}
                  />
                  <DetailRow
                    label="Principal Place Address"
                    value={data?.businessInfo?.principal_place_address}
                  />
                  <DetailRow
                    label="Business Pincode"
                    value={data?.business_pincode}
                  />
                </DetailsSection>
              ) : (
                <DetailsSection title="Business Details">
                  <DetailRow label="Shop Name" value={data?.shop_name} />
                  <DetailRow
                    label="Business Address"
                    value={data?.business_Address}
                  />
                  <DetailRow
                    label="Business Area"
                    value={data?.business_Area}
                  />
                  <DetailRow
                    label="Business City"
                    value={data?.business_city}
                  />
                  <DetailRow
                    label="Business State"
                    value={data?.business_state}
                  />
                  <DetailRow
                    label="Business Pincode"
                    value={data?.business_pincode}
                  />
                </DetailsSection>
              )}
            </Grid>
          </>
        )}
      </Grid>

      {isModalOpen && (
         <Box
          onClick={toggleModal}
          sx={{
            position: "fixed",
            inset: 0,
            bgcolor: "rgba(0,0,0,0.9)",
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
            zIndex: 9999,
          }}
        >
          <img
            src={`${baseUrlImage}${imgss}`}
            alt="Profile Large"
            style={{ maxWidth: "90%", maxHeight: "90%", borderRadius: 16 }}
          />
        </Box>
      )}
    </>
  );
}
export default Profile;
